@import "variables.scss";

.NavBar {
  display: flex;
  color: $c3;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  background-color: rgba($color: $c1, $alpha: 0.8);
  width: 100%;
  top: -10%;
  z-index: 99;
  overflow: hidden;
  height: 50px;
  animation: slideIn 3s;
  animation-delay: 3s;
  animation-fill-mode: forwards;

  .hide {
    display: none;
    cursor: pointer;
  }

  .fullName {
    display: none;
  }
  .Name {
    font-size: 1.5rem;
    padding: 1rem;
  }
  .rightLinks {
    padding: 1rem;
    display: flex;
    gap: 2rem;
  }
}

.home {
  color: white;
  text-decoration: none;
}

@media screen and (min-width: 900px) {
  .NavBar {
    animation: slideIn 3s;
    animation-delay: 3s;
    animation-fill-mode: forwards;
    background-color: $c1;
  }
  .hide {
    display: inline-block !important;
    text-decoration: none;
    color: white;
  }

  .Name {
    display: none !important;
  }
  .fullName {
    padding: 1rem;
    font-size: 2rem;
    display: inline !important;
  }
}

@keyframes slideIn {
  from {
    top: -10%;
  }
  to {
    top: 0%;
  }
}
